import React, {ChangeEvent, MouseEventHandler, useState} from 'react';

type ThreeOptionSelectType ={
    items:ItemsType[]
    value:number
}

type ItemsType = {
    value:number
    title:string
}

export const ThreeOptionSelect = (props:ThreeOptionSelectType) => {

    const TitleFind = props.items.find((el)=>el.value===props.value )


    const [editMode,setEdit]=useState(false)

    const ChangeStatus=() => {
        setEdit(!editMode)
    }

    const [select,SetSelect]=useState('Minsk')

    const ChangeEnableSpan =(e:MouseEvent<HTMLDivElement, MouseEvent>)=> {
        SetSelect(e.name)
    }

    return (
        <>
            {/*{editMode*/}
            {/*? <select autoFocus   onBlur={ChangeStatus}  onChange={ChangeEnableSpan}>*/}
            {/*    {props.items.map((el)=>{*/}
            {/*        return (*/}
            {/*        <option  key={el.value}>  {el.title}</option>*/}
            {/*    )*/}
            {/*    })}*/}
            {/*</select>*/}


            {/*: <span onDoubleClick={ChangeStatus}>{select}</span>}*/}
            {editMode
                ? <div>
                    {props.items.map((el)=>{
                        return (
                            <div onClick={ChangeEnableSpan}>{el.title}</div>
                        )
                    })}
                </div>
                : <span onClick={ChangeStatus}>{TitleFind && TitleFind.title}</span>
            }

        </>
    );
};

